.mainHeading {
  margin-bottom: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  letter-spacing: 1px;
  width: 100%;
  background: #4d49f6;
}
.cpivalue,
.spivalue {
  font-family: monospace;
  font-size: x-large;
}
.creditTotFind {
  font-size: 16px;
  font-style: italic;
}
.main1-wrapper {
  text-align: center;
  position: relative;
}
.creditTotalPingala {
  border-radius: 60px;
  z-index: 10;
  background: #035da7;
  padding: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: absolute;
  top: 10vh;
  animation: slideDown 0.3s ease-in-out;
  left: 25vw;
  border-radius: 16px;
  box-shadow: 1px 1px 16px #00223de0;
}
.creditTotalPingala > div {
  border-radius: 30px;
  background: #01325a;
}
@keyframes slideDown {
  0% {
    opacity: 0;
    top: -30vh;
  }
  100% {
    opacity: 1;
    top: 10vh;
  }
}
.creditTotalCalc {
  border-radius: 16px;
  width: 60vw;
  left: 20vw;
  z-index: 10;
  background: #00223d;
  padding: 20px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: absolute;
  top: 10vh;
  animation: slideDown 0.3s ease-in-out;
  box-shadow: 1px 1px 16px #035da76e;
}
@media screen and (max-width: 500px) {
  .creditTotalCalc {
    width: 100%;
    left: 0px;
  }
  .creditTotalPingala {
    left: 0px;
    width: 100%;
  }
}
